import echarts from 'echarts'
function pieLeft(){
  let option = {
    title : {
      // text: '',
      // x: 'center'
    },
    tooltip: {
      trigger: 'item',
      formatter: "{a} <br/>{b} : {c} ({d}%)"
    },
    legend: {
      orient: 'vertical',
      left: 'right',
      data: ['','','','','','','']
    },
    series : [
      {
        name: '',
        type: 'pie',
        radius : '55%',
        center: ['50%', '60%'],
        data:[
          {value:500, name:''},
          {value:300, name:''},
          {value:500, name:''},
          {value:200, name:''},
          {value:200, name:''},
          {value:700, name:''},
          {value:400, name:''}
        ],
        itemStyle: {
          emphasis: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        }
      }
    ]
  };

// app.currentIndex = -1;
  setInterval(function () {

    var dataLen = option.series[0].data.length;
    // 
    myChart3.dispatchAction({
      type: 'downplay',
      seriesIndex: 0,
      // dataIndex: app.currentIndex
    });
    // app.currentIndex = (app.currentIndex + 1) % dataLen;
    // 
    myChart3.dispatchAction({
      type: 'highlight',
      seriesIndex: 0,
      // dataIndex: app.currentIndex
    });
    //  tooltip
    myChart3.dispatchAction({
      type: 'showTip',
      seriesIndex: 0,
      // dataIndex: app.currentIndex
    });
  }, 1000);


//echarts
  var myChart3 = echarts.init(document.getElementById("pie_chartLeft"));

//
  myChart3.setOption(option);
  window.addEventListener("resize", function () {
    myChart3.resize()
  })
}
export default {
  pieLeft
}



