import echarts from 'echarts'
function pieRight(){
  let option = {
    title : {
      // text: '',
      // x: 'center'
    },
    tooltip: {
      trigger: 'item',
      formatter: "{a} <br/>{b} : {c} ({d}%)"
    },
    legend: {
      orient: 'vertical',
      left: 'right',
      data: ['','','','','','','']
    },
    series : [
      {
        name: '',
        type: 'pie',
        radius : '55%',
        center: ['50%', '60%'],
        // label:{
        //     normal:{
        //         position:"inner"
        //     }
        // },
        data:[
          {value:500, name:'20%'},
          {value:300, name:'6%'},
          {value:500, name:'26%'},
          {value:200, name:'6%'},
          {value:200, name:'5%'},
          {value:700, name:'32%'},
          {value:400, name:'5%'}
        ],
        itemStyle: {
          emphasis: {
            shadowBlur: 10,
            shadowOffsetX: 0,
            shadowColor: 'rgba(0, 0, 0, 0.5)'
          }
        }
      }
    ]
  };

// app.currentIndex = -1;
  setInterval(function () {

    var dataLen = option.series[0].data.length;
    // 
    myChart4.dispatchAction({
      type: 'downplay',
      seriesIndex: 0,
      // dataIndex: app.currentIndex
    });
    // app.currentIndex = (app.currentIndex + 1) % dataLen;
    // 
    myChart4.dispatchAction({
      type: 'highlight',
      seriesIndex: 0,
      // dataIndex: app.currentIndex
    });
    //  tooltip
    myChart4.dispatchAction({
      type: 'showTip',
      seriesIndex: 0,
      // dataIndex: app.currentIndex
    });
  }, 1000);


//echarts
  var myChart4 = echarts.init(document.getElementById("pie_chartRight"));

//
  myChart4.setOption(option);
  window.addEventListener("resize", function () {
    myChart4.resize()
  })
}
export default {
  pieRight
}



